@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200&display=swap);
@font-face {
  font-family: 'Milliard Book';
  font-style: normal;
  font-weight: normal;
  src: local('Milliard Book'), url("/project-ateris/static/media/Rene Bieder  Milliard Book.5ef3e82c.woff") format('woff');
}
@font-face {
  font-family: 'CircularStd-Black';
  src: local('CircularStd-Black'), url(/project-ateris/static/media/CircularStd-Black.757b8ac8.otf) format('otf');
}
@font-face {
  font-family: 'CircularStd-Book';
  src: local('CircularStd-Book'), url(/project-ateris/static/media/CircularStd-Book.90481370.otf) format('otf');
}

* {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
}

body {
  height: 100vh;
  width: 100vw;
  margin: 0px;
  padding: 0px;
  background-color: #000000;
  font-family: "CircularStd-Black", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.hide{
  display: none;
}

.svg_select{
  opacity: 1 !important;
}
.svg_select path{
  fill: #00B050 !important;
  color: #00B050 !important;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.player{
  display: flex;
  height: 90px;
  background-color: #181818;
  border-top: 1px solid #282828;
  margin: 0px !important;
}
.video_audio{
  display: none;
}
.album_image{
  height: 80px;
  width: 80px;
}
